.navbar-dark .navbar-nav .nav-link{
    font-size: 24px;
    padding-left: 15px;
    padding-right: 15px;
}

.navbar-brand{
    font-weight: bold;
    font-size: 30px;
}

section.header{
    background: url(../assets/images/bg_header.jpg);
    height: 90vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
}

.header-content .container{
    height: 90vh;
}

.header-content{
    width: 600px;
    color: white;
}

.header-content h1{
    font-size: 60px;
    font-weight: bold;
    margin-bottom: 30px;
}

.header-content button{
    margin-top: 20px;
    background-color: darkorange;
    border: none;
    padding: 10px 24px;
    font-size: 18px;
    font-weight: bold;
    color: white;
    border-radius: 25px;
}

.bg-dark{
    background-color: black !important;
}

.customizable{
    margin-top: 80px;
    color: salmon;
}

section.makeorder{
    height: 850px;
    background: url(../assets/images/MadeWithFish.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    opacity: 0.75;
    font-size: 40px;
    color: white;
}